import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'


// 引入element-plus
import 'element-plus/dist/index.css'
import ElementPlus from 'element-plus'
// 引入element-plus的图标库
import * as  ElementPlusIconsVue from '@element-plus/icons-vue'

import * as echarts from 'echarts';

import zhCn from 'element-plus/es/locale/lang/zh-cn'



const app = createApp(App)
// 全局注册el-icon
for (const [name, component] of Object.entries(ElementPlusIconsVue)) {
    app.component(name, component);
}

app.use(store).use(router).use(ElementPlus, { locale: zhCn, }).use(echarts).mount('#app')





// 根据设备的像素比设置视口的缩放比例，并禁止用户缩放
var pixclPatio = 1 / window.devicePixelRatio;
document.write('<meta name="viewport" content="width=device-width,initial-scale=' + pixclPatio + ',minimum-scale=' + pixclPatio + ',maximum-scale=' + pixclPatio + ',user-scalable=no" />');

// 获取HTML元素，并根据其宽度设置字体大小
var html = document.getElementsByTagName('html')[0];
var pageWidth = html.getBoundingClientRect().width;
html.style.fontSize = pageWidth / 254 + 'px';

// 监听浏览器窗口大小变化，并重新设置字体大小
window.onresize = function () {
    pageWidth = html.getBoundingClientRect().width;
    if (pageWidth < 2540) {
        pageWidth = 2540;
        console.log('当前设备宽度为2540px，rem大小为10px左右');
    }

    html.style.fontSize = pageWidth / 254 + 'px';

}


// 跳转的公用方法
function jump(url) {
    window.location.href = url;
}

app.config.globalProperties.jump = jump;


// 用于解决el-table的ResizeObserver loop completed with undelivered notifications 报错问题
const debounce = (fn, delay) => {
    // 防抖函数，用于减少高频率事件（如resize、scroll）的调用频率
    let timer
    return (...args) => {
        if (timer) {
            clearTimeout(timer)
        }
        timer = setTimeout(() => {
            fn(...args)
        }, delay)
    }
}

const _ResizeObserver = window.ResizeObserver;
window.ResizeObserver = class ResizeObserver extends _ResizeObserver {
    // 扩展原生的ResizeObserver类，使用防抖技术优化回调函数
    constructor(callback) {
        callback = debounce(callback, 200);
        super(callback);
    }
}